import React from "react";
import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import AppBar from "@material-ui/core/AppBar";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faHome,
  faUsers,
  faInfoCircle,
  faCogs,
  faMoneyBillWaveAlt,
  faPlusCircle,
  faChartLine,
  faChevronDown,
} from "@fortawesome/free-solid-svg-icons";
import { faBell, faListAlt } from "@fortawesome/free-regular-svg-icons";
import "../assets/css/style.css";
import $ from "jquery";
import { withRouter, Route } from "react-router-dom";
import { connect } from "react-redux";

import Divider from "@material-ui/core/Divider";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import MailIcon from "@material-ui/icons/Mail";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";

// import AdminSignup from '../adminSignup';

// Components

import AllStories from "../AllStories";
import AllArticles from "../AllArticles";
import AddEditor from "../addEditor";
import AllEditors from "../allEditor";

import HomePanel from "../home";
import CreateNewTopStory from "../createNewTopStory";
import CreateNewArticle from "../createNewArticle";


// Redux

import { logoutuser } from "../../redux/actions/authAction";


import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  NavbarText,
} from "reactstrap";
import { NavLink as RRNavLink } from "react-router-dom";

const drawerWidth = 241;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },

  changeColor: {
    backgroundColor: "white",
    height: 61,
  },
  appBarShift: {
    // width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },

  hide: {
    display: "none",
  },
  // drawer: {
  //     width: drawerWidth,
  //     flexShrink: 0,
  // },
  drawer: {
    [theme.breakpoints.up("sm")]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: "flex",
    alignItems: "center",
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
    justifyContent: "flex-end",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },

  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("sm")]: {
      display: "none",
    },
    color: "#f33676",
  },
}));

let mapStateToProps = (store) => {
  console.log(store);

  return {
    authState: store,
  };
};
let mapDispatchToProps = (disptch) => {
  return {
    logOut: () => {
      disptch(logoutuser());
    },
  };
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(function Header(props) {
    const classes = useStyles();
    const theme = useTheme();
    const { container } = props;
    const [mobileOpen, setMobileOpen] = React.useState(false);

    const handleDrawerToggle = () => {
      setMobileOpen(!mobileOpen);
    };

    const [open, setOpen] = React.useState(true);
    const [admin, setAdmin] = React.useState(false);

    const [dropdownOpen, setDropdownOpen] = React.useState(false);

    const toggle = () => setDropdownOpen((prevState) => !prevState);

    const logout = () => {
      props.logOut();
    };

    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar
          position="fixed"
          className={clsx(
            classes.appBar,
            {
              [classes.appBarShift]: open,
            },
            classes.changeColor
          )}
        >
          <div class="header">
            <div class="">
              <nav class="navbar custom_nav">
                <div class="collapse navbar-collapse" id="myNavbar">
                  <ul class="nav navbar-nav navbar-left">
                    <IconButton
                      color="inherit"
                      aria-label="open drawer"
                      edge="start"
                      onClick={handleDrawerToggle}
                      className={classes.menuButton}
                    >
                      <MenuIcon
                      // style={{

                      // }}
                      />
                    </IconButton>
                  </ul>
                  <ul class="nav navbar-nav navbar-right">
                    <UncontrolledDropdown
                      nav
                      isOpen={dropdownOpen}
                      toggle={toggle}
                    >
                      <DropdownToggle nav className="homeBtn navBtn">
                        Hi, Khuram
                        <FontAwesomeIcon
                          icon={faChevronDown}
                          style={{
                            fontSize: 12,
                            marginLeft: 5,
                            color: "grey",
                          }}
                        />
                      </DropdownToggle>
                      <DropdownMenu center style={{ width: "100px" }}>
                        {/* <DropdownItem
                          style={{
                            fontSize: 14,
                            background: "none",
                            border: "none",
                            color: "black",
                            width: "100%",
                            padding: 10,
                          }}
                        >
                          <Link to="/authentication_required" class="link">
                            {" "}
                            Settings
                          </Link>
                        </DropdownItem>
                        <DropdownItem divider /> */}
                        <DropdownItem
                          onClick={logout}
                          style={{
                            fontSize: 14,
                            background: "none",
                            border: "none",
                            color: "black",
                            width: "100%",
                            paddingBottom: 10,
                          }}
                        >
                          Log Out
                        </DropdownItem>
                      </DropdownMenu>
                    </UncontrolledDropdown>
                  </ul>
                  /admin/
                </div>
              </nav>
            </div>
          </div>
        </AppBar>
        <nav className={classes.drawer} aria-label="mailbox folders">
          {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
          <Hidden smUp implementation="css">
            <Drawer
              container={container}
              variant="temporary"
              anchor={theme.direction === "rtl" ? "right" : "left"}
              open={mobileOpen}
              onClose={handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper,
              }}
              ModalProps={{
                keepMounted: true, // Better open performance on mobile.
              }}
            >
              <div
                class="leftbar"
                style={{
                  width: "100%",
                }}
              >
                <div
                  class="logo_header"
                  style={{
                    width: 240,
                    borderBottom: "1px solid #f0f0f0",
                  }}
                >
                  <Link to="/">
                    <img
                      src={require("../assets/images/logo.png")}
                      class="logo"
                    />
                  </Link>
                </div>

                <span class="heading"></span>
                <div class="media hide">
                  <div class="media-body">
                    <p>Admin</p>
                    <h4 class="media-heading">Khuram</h4>
                  </div>
                </div>
                <ul>
                  {true ? (
                    <>
                      <li className="drawerLi">
                        <Link to="/" class="">
                          <FontAwesomeIcon
                            icon={faHome}
                            className="iconDrawer"
                          />
                          Home
                        </Link>
                      </li>

                      <li>
                        <Link to="/admin/all_articles" class="">
                          <FontAwesomeIcon
                            icon={faUsers}
                            className="iconDrawer"
                          />
                          All Articles
                        </Link>
                      </li>
                      <li>
                        <Link to="/admin/AllStories" class="">
                          <FontAwesomeIcon
                            icon={faListAlt}
                            className="iconDrawer"
                          />
                          All Stories
                        </Link>
                      </li>

                      <li>
                        <Link to="/admin/all_editor" class="">
                          <FontAwesomeIcon
                            icon={faInfoCircle}
                            className="iconDrawer"
                          />
                          All Editors
                        </Link>
                      </li>
                    </>
                  ) : null}
                </ul>
              </div>
            </Drawer>
          </Hidden>
          <Hidden xsDown implementation="css">
            <Drawer
              classes={{
                paper: classes.drawerPaper,
              }}
              variant="permanent"
              open
            >
              <div
                class="leftbar"
                style={{
                  width: "100%",
                }}
              >
                <div
                  class="logo_header"
                  style={{
                    width: 240,
                    borderBottom: "1px solid #f0f0f0",
                  }}
                >
                  <Link to="/">
                    <img
                      src={require("../assets/images/logo.png")}
                      class="logo"
                    />
                  </Link>
                </div>

                <span class="heading"></span>
                <div class="media hide">
                  <div class="media-body">
                    <p>Admin</p>
                    <h4 class="media-heading">John Doe</h4>
                  </div>
                </div>
                <ul>
                  {true ? (
                    <>
                      <NavItem>
                        <NavLink
                          to="/"
                          activeClassName="active"
                          exact
                          tag={RRNavLink}
                        >
                          <FontAwesomeIcon
                            icon={faHome}
                            className="iconDrawer"
                          />
                          Home
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink
                          to="/admin/all_articles"
                          activeClassName="active"
                          tag={RRNavLink}
                        >
                          <FontAwesomeIcon
                            icon={faUsers}
                            className="iconDrawer"
                          />
                          All Articles
                        </NavLink>
                      </NavItem>

                      <NavItem>
                        <NavLink
                          to="/admin/AllStories"
                          activeClassName="active"
                          tag={RRNavLink}
                        >
                          <FontAwesomeIcon
                            icon={faListAlt}
                            className="iconDrawer"
                          />
                          All Top Stories
                        </NavLink>
                      </NavItem>
                      <NavItem>
                        <NavLink
                          to="/admin/all_editor"
                          activeClassName="active"
                          tag={RRNavLink}
                        >
                          <FontAwesomeIcon
                            icon={faInfoCircle}
                            className="iconDrawer"
                          />
                          All Editor
                        </NavLink>
                      </NavItem>
                    </>
                  ) : null}
                </ul>
              </div>
            </Drawer>
          </Hidden>
        </nav>

        <main
          className={clsx(classes.content, {
            [classes.contentShift]: open,
          })}
        >
          <div className={classes.drawerHeader} />

          <Route
            exact
            path="/"
            component={() => {
              return <HomePanel admin={admin} />;
            }}
          />
          <Route exact path="/admin/AllStories" component={AllStories} />
          <Route
            exact
            path="/admin/create_new_top_story"
            component={CreateNewTopStory}
          />
          <Route exact path="/admin/all_articles" component={AllArticles} />
          <Route
            exact
            path="/admin/create_new_article"
            component={CreateNewArticle}
          />

          <Route exact path="/admin/add_editor" component={AddEditor} />
          <Route exact path="/admin/all_editor" component={AllEditors} />

          {/* <Route exact path="/expenseCategory" component={ExpenseCategory} />
          <Route exact path="/expenseList" component={Admins} />
          <Route exact path="/edit_company" component={NewCompany} />
          <Route exact path="/editSettings" component={EditSetting} />
          <Route exact path="/editexpenseGroup" component={EditExpenseGroup} />
          <Route exact path="/payment-type" component={PaymentType} />
          <Route exact path="/editPaymentType" component={EditPaymentType} />
          <Route exact path="/companyDeshboard" component={CompanyDeshboard} />
          <Route exact path="/add_employee" component={AddEmployee} />
          <Route exact path="/add_new_employee" component={AddEmployeeForm} />
          <Route
            exact
            path="/authentication_required"
            component={SettingCompany}
          />
          <Route
            exact
            path="/edit_company_setting"
            component={EditSettingCompany}
          />
          <Route exact path="/expense_status" component={ExpenseStatus} />
          <Route exact path="/expense_status_hr" component={ExpenseListHr} />
          <Route exacct path="/company_expense" component={companyExpenses} />
          <Route
            exact
            path="/admin_company_empoyees"
            component={adminEmployeesList}
          /> */}
        </main>
      </div>
    );
  })
);
