import axios from "axios";
import {
  CREATE_ARTICLE,
  COMPANIES_LIST,
  DELETE_ARTICLE,
  EDIT_ARTICLE,
} from "../constat";
import API from '../url'

export function createArticle(body) {
  return (dispatch) => {
    let token = localStorage.getItem("token");

    const config = {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    };
    let formData = new FormData();

    for (var item in body) {
      formData.append(item, body[item]);
    }
    console.log(formData);
    axios
      .post(API + "/create_article", formData, config)
      .then((resp) => {
        // successFul("success");
        dispatch({
          type: CREATE_ARTICLE,
          payload: resp.data,
        });
      })
      .catch((err) => {
        // createdFail("err");
        console.log(err);
      });
  };
}

export function deleteArticle(id) {
  return (dispatch) => {
    axios.post(API + `/delete_article`, {id}).then((resp) => {
      console.log(resp.data);
      dispatch({
        type: DELETE_ARTICLE,
        payload: resp.data,
      });
    });
  };
}

export function editArticle(id, body) {
  return (dispatch) => {
    let token = localStorage.getItem("token");

    let header = {
      headers: {
        "Content-Type": "application/json",

        "x-sh-auth": token,
      },
    };
    let {
      company_name,
      employee_limit,
      work_email,
      ph_no,
      services,
      expense_image,
      expense_group,
      payment_type,
      email_template,
      image_file,
    } = body;
    let newCompany = {
      company_name,
      employee_limit,
      work_email,
      ph_no,
      services,
      expense_group,
      expense_image,
      payment_type,
      email_template,
      image_file,
    };
    axios
      .post(
        `https://mr-expense-backend.herokuapp.com/admin/edit_company?_id=${id}`,
        newCompany,
        header
      )
      .then((resp) => {
        dispatch({
          type: EDIT_ARTICLE,
          payload: resp.data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };
}

