import React, { useState } from "react";
import { withRouter, Link } from "react-router-dom";
import { connect, useDispatch } from "react-redux";
import IntlTelInput from "react-intl-tel-input";
import "react-intl-tel-input/dist/main.css";
import { createArticle } from "../redux/actions/ArticleAction";

import Toast from "light-toast";
import axios from "axios";

import { Row, Col, Container } from "reactstrap";
import { Editor } from "@tinymce/tinymce-react";

const CreateNewArticle = () => {
  let dispatch = useDispatch()
  const [state, setState] = useState({
    description: null,
    title: null,
    image: null,
    // admin_id: 
  });

  
  const onSubmit = (e) => {
    e.preventDefault();
    dispatch(createArticle(state))
  };

  const onChangeHandler = (e) => {
    if (e.target.name == "image") {
      let val = e.target.files[0];
      setState({...state,
        [e.target.name]: val,
      });
    } else {
      setState({...state,
        [e.target.name]: e.target.value,
      });
    }
  };

  return (
    <>
      <div class="content-wrapper">
        <div
          id="order_preview"
          class="wow fadeInUp content_box"
          style={{ visibility: "visible", animationName: "fadeInUp" }}
        >
          <Row className="table-header">
            <Col xs="12" md="12">
              <h2 class="section-title">New Article</h2>
            </Col>
          </Row>
          <div class="row">
            <Row>
              <Col xs="12" md="8">
                <form>
                  <div class="form-group">
                    <label for="pwd">Article Title</label>
                    <input
                      type="text"
                      class="form-control"
                      required
                      name="title"
                      onChange={onChangeHandler}
                      value={state.title}
                    />
                  </div>
                  <div class="form-group">
                    <label for="pwd">Article Descriptions</label>
                    <Editor
                      apiKey="492p5e1l7v1fdjswe4kmjmp2lqjdr55bp9bsysnj6dskvf6l"
                      init={{
                        height: 300,
                        menubar: false,
                        plugins: [
                          "advlist autolink lists link image",
                          "charmap print preview anchor help",
                          "searchreplace visualblocks code",
                          "insertdatetime media table paste wordcount",
                        ],
                        toolbar:
                          "undo redo | formatselect | bold italic | \
            alignleft aligncenter alignright | \
            bullist numlist outdent indent | help",
                      }}
                      onChange={(e) => {
                        let content = e.target.getContent();
                        setState({
                          ...state,
                          description: content,
                        });
                      }}
                      multiline={true}
                    />
                  
                  </div>

                  <div class="form-group">
                    <label for="pwd">Upload Image</label>
                    <input
                      type="file"
                      class="form-control"
                      required
                      name="image"
                      onChange={onChangeHandler}
                    />
                  </div>

                  <button
                    type="submit"
                    class="btn btn-default"
                    onClick={onSubmit}
                    style={{
                      marginTop: 10,
                      float: "right",
                      padding: "8px 16px",
                      fontSize: 16,
                    }}
                  >
                    Create
                  </button>
                </form>
              </Col>
            </Row>
          </div>
        </div>
      </div>
    </>
  );
};

