import { CREATE_ARTICLE, DELETE_ARTICLE, EDIT_ARTICLE } from "../constat";
import history from "../../components/history/history";

let initState = {
  msg: null,
  isArticleDeleted: null,
  companyEditMsg: null,
};

export function ArticleReducer(state = initState, action) {
  switch (action.type) {
    case CREATE_ARTICLE:
      state.msg = "Company Succesfully Created.";

      break;

    case DELETE_ARTICLE:
      state.isArticleDeleted = action.payload;
      setTimeout(() => {
        state.isArticleDeleted = null;
      });

      break;

    case EDIT_ARTICLE:
      state.companyEditMsg = "Company Successfully Edited";
      setTimeout(() => {
        history.push("/");
      });

      break;

    default:
      break;
  }
  return state;
}
