import React from "react";
import { withRouter, Link } from "react-router-dom";
import { connect } from "react-redux";
import IntlTelInput from "react-intl-tel-input";
import "react-intl-tel-input/dist/main.css";
import { createStory } from "../redux/actions/topStoryAction";

import Toast from "light-toast";
import axios from "axios";

import { Row, Col, Container } from "reactstrap";
import { Editor } from "@tinymce/tinymce-react";

class NewCompany extends React.Component {
  constructor() {
    super();

    successFul = (parm) => {
      if (parm === "success") {
        Toast.success("Successfully Created...!!", 2000, () => {});
        this.setState({
          description: null,
          storytitle: null,
          image: null,
        });
      }
    };
    createdFail = (parm) => {
      if (parm === "err") {
        Toast.fail("Email Already in use, Try Again.", 2000, () => {});
      }
    };
    successFul();
    createdFail();
  }

  state = {
    description: null,
    storytitle: null,
    image: null,
  };

  onChangeHandler = (e) => {
    if (e.target.name == "image") {
      let val = e.target.files[0];
      this.setState({
        [e.target.name]: val,
      });
    } else {
      this.setState({
        [e.target.name]: e.target.value,
      });
    }
  };

  emailRequired = () => {
    Toast.fail("Fill all the fields, Try Again.", 2000, () => {});
  };

  onSubmit = (e) => {
    e.preventDefault();
    let { storytitle, description, image } = this.state;
    this.props.crete_story(this.state);
  };

  render() {
    return (
      <>
        <div class="content-wrapper">
          <div
            id="order_preview"
            class="wow fadeInUp content_box"
            style={{ visibility: "visible", animationName: "fadeInUp" }}
          >
            <Row className="table-header">
              <Col xs="12" md="12">
                <h2 class="section-title">New Story</h2>
              </Col>
            </Row>
            <div class="row">
              <Row>
                <Col xs="12" md="8">
                  <form>
                    <div class="form-group">
                      <label for="pwd">Story Title</label>
                      <input
                        type="text"
                        class="form-control"
                        required
                        name="storytitle"
                        onChange={this.onChangeHandler}
                        value={this.state.storytitle}
                      />
                    </div>
                    <div class="form-group">
                      <label for="pwd">Story Descriptions</label>
                      {/* <input
                        type="text"
                        class="form-control"
                        required
                        name="description"
                        onChange={this.onChangeHandler}
                        value={this.state.description}
                      /> */}
                      <Editor
                        apiKey="492p5e1l7v1fdjswe4kmjmp2lqjdr55bp9bsysnj6dskvf6l"
                        init={{
                          height: 300,
                          menubar: false,
                          plugins: [
                            "advlist autolink lists link image",
                            "charmap print preview anchor help",
                            "searchreplace visualblocks code",
                            "insertdatetime media table paste wordcount",
                          ],
                          toolbar:
                            "undo redo | formatselect | bold italic | \
            alignleft aligncenter alignright | \
            bullist numlist outdent indent | help",
                        }}
                        onChange={(e) => {
                          let content = e.target.getContent();
                          this.setState({
                            description: content,
                          });
                        }}
                        multiline={true}
                      />
                    </div>

                    <div class="form-group">
                      <label for="pwd">Upload Image</label>
                      <input
                        type="file"
                        class="form-control"
                        required
                        ref=""
                        name="image"
                        onChange={this.onChangeHandler}
                      />
                    </div>

                    <button
                      type="submit"
                      class="btn btn-default"
                      onClick={this.onSubmit}
                      style={{
                        marginTop: 10,
                        float: "right",
                        padding: "8px 16px",
                        fontSize: 16,
                      }}
                    >
                      Create
                    </button>
                  </form>
                </Col>

                {/* <Col xs="12" md="5">
                  <form encType="multipart/form-data">
                     

                    <button
                      type="submit"
                      class="btn btn-default"
                      onClick={this.onSubmit}
                      style={{
                        marginTop: 10,
                        float: "right",
                        padding: "8px 16px",
                        fontSize: 16,
                      }}
                    >
                      Create
                    </button>
                  </form>
                </Col> */}
              </Row>
            </div>
          </div>
        </div>
      </>
    );
  }
}

let mapStateToProps = (store) => {
  return {
    admin: store.AdminReducer,
    com: store.companyReducer,
  };
};

let mapDispatchToProps = (dispatch) => {
  return {
    crete_story: (data) => {
      dispatch(createStory(data));
    },
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(NewCompany)
);
export let successFul, createdFail, getPaymentType, getExpenseGroup;
